import React, { useState, useRef } from 'react';
import { NavigationContainer, NavigationContainerRef, LinkingOptions } from '@react-navigation/native';
import { SafeAreaProvider } from 'react-native-safe-area-context';
import { HelmetProvider } from 'react-helmet-async';
import { RootStackParamList } from './src/types/navigation';
import { ErrorBoundary } from './src/components/ErrorBoundary';
import { AppNavigator } from './src/navigation/AppNavigator';
import { LoadingIndicator } from './src/components/LoadingIndicator';
import { routes } from './src/utils/routes';

export default function App() {
  const [searchQuery, setSearchQuery] = useState('');
  const navigationRef = useRef<NavigationContainerRef<RootStackParamList>>(null);

  const linking: LinkingOptions<RootStackParamList> = {
    prefixes: ['https://foilquiver.com', 'http://localhost:19006'],
    config: {
      screens: {
        browse: '',
        terms: 'terms',
        privacy: 'privacy',
        contact: 'contact',
        comparison: 'comparison',
        brand: 'brand/:brandName',
        foil: 'foil/:brandName/:modelName',
      },
    },
    getInitialURL() {
      const url = new URL(window.location.href);
      const searchQuery = url.searchParams.get('searchQuery') || '';
      return `/?searchQuery=${searchQuery}`;
    },
  };

  const handleSearch = (text: string) => {
    setSearchQuery(text);
    if (navigationRef.current) {
      navigationRef.current.setParams({ searchQuery: text });
    }
  };

  return (
    <HelmetProvider>
      <ErrorBoundary>
        <SafeAreaProvider>
          <NavigationContainer 
            ref={navigationRef}
            linking={linking}
            fallback={<LoadingIndicator />}
          >
            <AppNavigator 
              searchQuery={searchQuery}
              onSearch={handleSearch}
            />
          </NavigationContainer>
        </SafeAreaProvider>
      </ErrorBoundary>
    </HelmetProvider>
  );
} 